.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.searchInputs {
  margin-bottom: 100px;
  display: flex;
  box-shadow: 0 2px 15px -4px gray;
}

.search input{
  background-color: white;
  border: 0;
  border-radius: 2px;
  padding: 5px;
  font-size: 18px;

}

 .blog-preview {
   padding: 15px;
 }

 .selfText {
   color: grey;
   padding: 50px;

 }